/* open-sans-300 - latin */
@font-face {
  font-family: "Open Sans Light";
  font-style: normal;
  font-weight: 300;
  src: url('/hehp/assets/fonts/OpenSans/open-sans-v18-latin-300.eot'); /* IE9 Compat Modes */
  src: local(""),
    url('/hehp/assets/fonts/OpenSans/open-sans-v18-latin-300.woff2')
      format("woff2"),
    /* Super Modern Browsers */
      url('/hehp/assets/fonts/OpenSans/open-sans-v18-latin-300.woff')
      format("woff");
}

/* open-sans-regular - latin */
@font-face {
  font-family: "Open Sans Regular";
  font-style: normal;
  font-weight: 400;
  src: url('/hehp/assets/fonts/OpenSans/open-sans-v18-latin-regular.eot');
  src: local(""),
    url('/hehp/assets/fonts/OpenSans/open-sans-v18-latin-regular.woff2')
      format("woff2"),
    /* Super Modern Browsers */
      url('/hehp/assets/fonts/OpenSans/open-sans-v18-latin-regular.woff')
      format("woff");
}

/* text */

$body-font: "Open Sans Regular", "Verdana", "sans-serif";
$heading-font: "Open Sans Light", "Verdana", "sans-serif";

/* Colors */

$dark-background: #841212;
$light-text: #F3F3F3;
$light-link: #9B9B9B;
$light-link-hover: #fff;
$medium-background: #801515;
$light-background: #f7e4ef;
$dark-text: #444444;
$code: #3c1553;
$muted-text: #777777;

/* Image Heights */

$parallax-height: 350px;

/* Viewer proportions */

$viewer-height: 100%;
$max-width: 100%;

@import "facets";
